import { Component } from "react";
import CardList from "./CardList";
import { robots } from "./robots";
import SearshBox from "./SearchBox";
import "./App.css";
import Scroll from "./Scroll";
class App extends Component {
  constructor() {
    super();
    this.state = {
      robots: robots,
      searshField: "",
    };
  }

  searchChange = (event) => this.setState({ searshField: event.target.value });

  render() {
    const filt = this.state.robots.filter((robot) => {
      return robot.name.toLowerCase().includes(this.state.searshField);
    });
    return (
      <div className="tc">
        <h1 id="title">robonas is here</h1>
        <SearshBox searchChange={this.searchChange} />
        <Scroll>
          <CardList robots={filt} />
        </Scroll>
      </div>
    );
  }
}

export default App;
