import React from "react";
import Card from "./Card";
const CardList = ({ robots }) => {
  return (
    <div>
      {robots.map((arr, i) => {
        return (
          <Card
            key={i}
            name={robots[i].name}
            id={robots[i].id}
            email={robots[i].email}
            username={robots[i].username}
          />
        );
      })}
    </div>
  );
};
export default CardList;
